:root {
  --color-brand--1: #ffb545;
  --color-brand--2: #00c46a;
  --color-dark--1: #2d3439;
  --color-dark--2: #42484d;
  --color-light--1: #aaa;
  --color-light--2: #ececec;
  --color-light--3: #d6dee0;
}

* {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  color: var(--color-light--2);
  height: 100vh;
  overscroll-behavior-y: none;
  background-color: #fff;
  padding: 2.5rem;
  font-family: Manrope, sans-serif;
  font-weight: 400;
  line-height: 1.6;
  display: flex;
}

a:link, a:visited {
  color: var(--color-brand--1);
}

.sidebar {
  background-color: var(--color-dark--1);
  flex-direction: column;
  flex-basis: 60rem;
  padding: 2rem 3rem;
  display: flex;
}

.logo {
  height: 5.2rem;
  align-self: center;
  margin-bottom: 4rem;
}

.workouts {
  height: 77vh;
  list-style: none;
  overflow-x: hidden;
  overflow-y: scroll;
}

.workouts::-webkit-scrollbar {
  width: 0;
}

.workout {
  background-color: var(--color-dark--2);
  cursor: pointer;
  border-radius: 5px;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: .75rem 1.5rem;
  margin-bottom: 1.75rem;
  padding: 1.5rem;
  display: grid;
}

.workout--running {
  border-left: 5px solid var(--color-brand--2);
}

.workout--cycling {
  border-left: 5px solid var(--color-brand--1);
}

.workout__title {
  grid-column: 1 / -1;
  font-size: 1.7rem;
  font-weight: 600;
}

.workout__details {
  align-items: baseline;
  display: flex;
}

.workout__icon {
  height: .28rem;
  margin-right: .2rem;
  font-size: 1.8rem;
}

.workout__value {
  margin-right: .5rem;
  font-size: 1.5rem;
}

.workout__unit {
  color: var(--color-light--1);
  text-transform: uppercase;
  font-size: 1.1rem;
  font-weight: 800;
}

.form {
  background-color: var(--color-dark--2);
  height: 9.25rem;
  border-radius: 5px;
  grid-template-columns: 1fr 1fr;
  gap: .5rem 2.5rem;
  margin-bottom: 1.75rem;
  padding: 1.5rem 2.75rem;
  transition: all .5s, transform 1ms;
  display: grid;
}

.form.hidden {
  height: 0;
  opacity: 0;
  margin-bottom: 0;
  padding: 0 2.25rem;
  transform: translateY(-30rem);
}

.form__row {
  align-items: center;
  display: flex;
}

.form__row--hidden {
  display: none;
}

.form__label {
  flex: 0 0 50%;
  font-size: 1.5rem;
  font-weight: 600;
}

.form__input {
  width: 100%;
  background-color: var(--color-light--3);
  border: none;
  border-radius: 3px;
  padding: .3rem 1.1rem;
  font-family: inherit;
  font-size: 1.4rem;
  transition: all .2s;
}

.form__input:focus {
  background-color: #fff;
  outline: none;
}

.form__btn {
  display: none;
}

.copyright {
  text-align: center;
  color: var(--color-light--1);
  margin-top: auto;
  font-size: 1.3rem;
}

.twitter-link:link, .twitter-link:visited {
  color: var(--color-light--1);
  transition: all .2s;
}

.twitter-link:hover, .twitter-link:active {
  color: var(--color-light--2);
}

.button-styled {
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  text-align: center;
  text-rendering: geometricprecision;
  text-transform: none;
  -webkit-user-select: none;
  user-select: none;
  touch-action: manipulation;
  vertical-align: middle;
  border-radius: 6px;
  font-family: nunito, roboto, proxima-nova, proxima nova, sans-serif;
  font-weight: 800;
  display: inline-block;
  box-shadow: 1px 2px 4px #0000001a;
}

.button-styled:hover, .button-styled:active {
  background-color: initial;
  color: #ef6863;
  background-position: 0 0;
}

.modify {
  background-color: #add8e6;
}

.modify:hover {
  color: #add8e6;
}

.delete {
  background: #ef6863;
}

.delete_all {
  background: #ef6863;
  margin-bottom: 5%;
  padding: .5rem 1.25rem;
}

#show-all-workouts {
  background-color: #55c94d;
}

#show-all-workouts:hover {
  color: #309830;
}

#map-container {
  height: 90vh;
  width: 100vw;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

#map {
  height: 90vh;
  width: 70vw;
}

#show-all-workouts {
  margin-top: 20px;
  padding: 10px 30px;
}

.leaflet-popup .leaflet-popup-content-wrapper {
  background-color: var(--color-dark--1);
  color: var(--color-light--2);
  border-radius: 5px;
  padding-right: .6rem;
}

.leaflet-popup .leaflet-popup-content {
  font-size: 1.5rem;
}

.leaflet-popup .leaflet-popup-tip {
  background-color: var(--color-dark--1);
}

.running-popup .leaflet-popup-content-wrapper {
  border-left: 5px solid var(--color-brand--2);
}

.cycling-popup .leaflet-popup-content-wrapper {
  border-left: 5px solid var(--color-brand--1);
}

.btn {
  color: #fff;
  cursor: pointer;
  width: 15rem;
  background-color: #0000;
  border: 2px solid #fff;
  border-radius: 1rem;
  grid-column: span 2;
  justify-content: center;
  align-items: center;
  padding: .5rem 1rem;
  font-weight: bold;
}

.drop_down {
  height: 40%;
  margin-right: 5%;
}

.container {
  padding-left: 45%;
  display: flex;
}

.weather-img {
  height: 30px;
  width: 30px;
}

.weather_info {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.weather-container {
  flex-direction: row;
  display: flex;
}

